const app = getApp();
export default {
	data() {
		return {
			rows: []
		};
	},
	onShow() {
		this.getInvoiceList();
	},
	methods: {
		getInvoiceList() {
			this.rows = [];
			app.globalData.wxDB
				.collection('InvoiceManage')
				.where({
					_openid: uni.getStorageSync('user.openid')
				})
				.get()
				.then(res => {
					if (res.errMsg == 'collection.get:ok') {
						this.rows = res.data;
					}
				})
				.catch(console.error);
		},
		openAdd() {
			uni.navigateTo({
				url: './ae'
			});
		},
		openEdit(e) {
			uni.navigateTo({
				url: './ae?item='+encodeURIComponent(JSON.stringify(e.currentTarget.dataset.item)) 
			});
		}
	}
};
